<template>
  
  <div></div>
  <div v-if="this.$store.state.isLoggedIn">
    <button @click="GetOut">Logout</button>
  </div>
  <div v-show="this.$store.state.isLoggedIn">
    <button ref="sTart" @click="heLLo">Home</button> 
    <button @click="goBack">Previous</button>
    <button @click="goNext">Next</button>
  </div>
  
  <!-- <div> Update number: {{ needUpdate }}</div> -->

  <!-- <MeNu v-if="!this.$store.state.isLoggedIn"/>
  <Home v-if="this.$store.state.isLoggedIn"/>
  <div v-show="!this.$store.state.isLoggedIn">
    <router-view v-show="!this.$store.state.isLoggedIn"></router-view>
  </div>
   -->
  <!-- <ConneIon/>
  <NewUser/> -->
  <div v-show="this.$store.state.showParent">
    <component :is="activeComp"></component>
  </div>
    <!-- <router-view v-if="this.$store.state.categorie"></router-view> -->
    <router-view></router-view>

  <p>
        <br>
        <!-- with ACCESS {{ this.$store.state.user.access }} <br>
        with REFRESH {{ this.$store.state.user.refresh }} <br>
        <button @click="Refre">Get Refresh</button>
        
        <br><br>
        Actual Route: {{ this.$store.state.actualRoute }} <br>
        Active Component: {{ this.activeComp }} <br>
        Is Active? : {{ this.isActive }} -->

        <br> <br>

        <for-admin v-if="this.$store.state.isAdmin "></for-admin>
  </p>

</template>

<script>
import ForAdmin from './components/for-admin.vue'
// import ConneIon from './components/conne-ion.vue'
// import MeNu from './components/me-nu.vue'
// import Home from './components/home-options.vue'
// import NewUser from './components/new-user.vue'

export default {
  name: 'App',
  data(){
    return {
      isActive : false,
      activeComp: '',
      needUpdate: 1,
    }
  },
  components: {
    ForAdmin
    // ConneIon,
    // MeNu,
    // NewUser,
    // Home,
  },
  beforeCreate(){
    console.log("BeforeCreate says: ", this.$store.state.isLoggedIn)
  },
  created(){
    const token = localStorage.getItem('token')
    const refresh = localStorage.getItem('refresh')
    const jwt = {
      'access' : token,
      'refresh' : refresh,
    }
    this.$store.state.user = jwt
    this.heLLo()
    console.log("'CREATED' Now isLoggedIn? ", this.$store.state.isLoggedIn)
    this.isActive = this.$store.state.isLoggedIn
    if ( !this.$store.state.isLoggedIn ){
      this.activeComp = "me-nu"
    }else{
      this.activeComp = "ho-me"
    }
    // this.savedToken = "jovee"
  },
  beforeMount(){
    console.log("BeforeMount says: ", this.$store.state.isLoggedIn)
  },
  mounted(){
    console.log("Mounted says ", this.$store.state.isLoggedIn)
  },
  beforeUpdate(){
    console.log("BeforeUpdate says : ", this.$store.state.isLoggedIn)
  },
  updated(){
    this.isActive = this.$store.state.isLoggedIn
    if ( !this.$store.state.isLoggedIn){
      this.activeComp = "me-nu"
    }else{
      this.activeComp = "ho-me"
    }
    console.log("UPDATED isLoggedIn :", this.$store.state.isLoggedIn)
    console.log("UPdate NUMBERER : ", this.needUpdate)
  },
  beforeUnmount(){
    console.log("BeforeUnMount says : ", this.$store.state.isLoggedIn)
  },
  methods:{
        goBack(){
          console.log("You are about to go back in your navigation")
          this.$router.back()
        },
        goNext(){
          console.log("You are about to go foward in your navigations")
          this.$router.forward()
        },
         GetOut(){
          this.$store.state.user.access = ''
          this.$store.state.user.refresh = ''
          localStorage.setItem('accessToken', '')
          this.$store.state.isLoggedIn = false
          console.log("You are logged out")
          this.$router.push('/connexion')
        },
        Refre(){
          console.log("THe refresh Token is: ", this.$store.state.user.refresh)
          fetch('http://127.0.0.1:8000/jov/api/refresh/',{
            method: 'POST',
            headers: {
              'Content-Type' : 'application/json',
            },
            body: JSON.stringify({
              refresh : this.$store.state.user.refresh
            }),
          }).then((response)=>{
                if (!response.ok){
                  this.$store.state.isLoggedIn = false
                  throw new Error("Something went wrongg")
                }
                this.$store.state.isLoggedIn = true
                return response.json()
            }).then((data)=>{
                // console.log("THe actual token is: ", this.$store.state.user.access )
                // console.log("The new refreshed Toke is: ", data)

                this.$store.state.user.access = data.access
                localStorage.setItem('accessToken', data.access)
                const jov = localStorage.getItem('accessToken')
                console.log("we have now: ", jov)
            }).catch((error)=>{
              console.log("THis is the error: ", error)
            })
        },
        heLLo(){
            // This is to check whether the access token is still valid
            try {
              fetch('http://127.0.0.1:8000/jov/api/userquery/isLoggedin/',{
                method : 'POST',
                headers: {
                    'Content-Type' : 'application/json',
                    'Authorization' : 'Bearer '+ this.$store.state.user.access
                    // 'Cookie' : this.$store.state.token,
                },
            }).then((response)=>{
              if (response.ok){
                console.log("Seems that things are right")
                // this.isActive = true
                this.$store.state.isLoggedIn = true
              } else {
                // this.isActive = false
                this.$store.state.isLoggedIn = false
              }
              return response.json()
            }).then((data)=>{
                console.log("The response is: ", data)
                this.$store.state.isAdmin = data.is_Admin
                // this.$store.state.isLoggedIn = true

            }).catch((error)=>{
              // this.$store.state.isLoggedIn = false
              // this.$store.state.isLoggedIn = false
              console.log("THis is the error: ", error.message)
            })
            } catch(error){
              console.log("THE ERROR WE CAPTURED Is :", error.message)
            } finally {
              console.log("THE PROCESS TERMINATED")
            }
            console.log("HELLO made isLoggedIn to: ", this.$store.state.isLoggedIn)
            // this.updated()
            this.needUpdate++
            this.$router.push('/')
        },
      }

}
</script>

